<template>
  <div class="page">
    <ul class="bar">
      <li
        v-for="(item, index) in barList"
        :key="index"
        :class="index === activeIndex ? 'active' : ''"
        @click="tapBar(index)"
      >{{item}}</li>
      <div :style="`left: ${activeIndex * 25}%`" class="activeBg"></div>
    </ul>
    <!-- 图片区域 -->
    <v-touch @swiperight="swiperight" @swipeleft="swipeleft" class="imgArea">
      <transition name="fade" v-for="(img, index) in imgList" :key="index">
        <div v-if="index === activeIndex" class="imgWrapper">
          <img alt="img" :src="img" />
        </div>
      </transition>
    </v-touch>
    <div class="title">
      2020年1月
    </div>
    <div class="desc">
      青海省的阳光慢慢变成可以使用的电能，它即将来到你的身边
    </div>
  </div>
</template>
<script>
import img1 from '../assets/page2-img-1.png'
import img2 from '../assets/page2-img-2.png'
import img3 from '../assets/page2-img-3.png'
import img4 from '../assets/page2-img-4.png'
export default {
  name: 'PageSecond',
  data: () => {
    const barList = [
      '发电',
      '采购',
      '调配',
      '充电'
    ]
    const imgList = [
      img1,
      img2,
      img3,
      img4
    ]
    const activeIndex = 0
    return {
      imgList,
      barList,
      activeIndex,
      clientX: 0,
      moveX: 0,
      isDown: false
    }
  },
  methods: {
    tapBar (key) {
      this.activeIndex = key
    },
    swiperight () {
      const nextIndex = this.activeIndex - 1
      this.activeIndex = nextIndex < 0 ? 0 : nextIndex
    },
    swipeleft () {
      const nextIndex = this.activeIndex + 1
      this.activeIndex = nextIndex > 3 ? 3 : nextIndex
    }
  }
}
</script>
